import * as yup from 'yup'

export const publishArticleValidation = yup.object().shape({
  title: yup.string().required('Tytu jest wymagany').min(1, 'Tytu jest wymagany'),
  text: yup.string().required('Wstp jest wymagany').min(1, 'Wstp jest wymagany'),
  category: yup.object().nullable().required('Kategoria jest wymagana'),
  chapters: yup.array().of(yup.object().shape({
    subtitle: yup.string().required('Tytu rozdziau jest wymagany').min(1, 'Tytu rozdziau jest wymagany'),
    text: yup.string().required('Tre rozdziau jest wymagana').min(1, 'Tre rozdziau jest wymagana'),
  }))
})